<template>
  <div class="card" style="height: 95%;">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Maintenance Status</span>
        </b-col>
        <!-- <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'New'"
              classButton="button button--new"
              classIcon="icon-style-1"
              :disabled="ButtonStatus == null ? true : !ButtonStatus.btnAddNewProspect"
              @click="doAddNewProspect"
            />
          </span>

          <span>
            <ABSButton
              :text="'View All'"
              classButton="button button--new"
              classIcon="icon-style-1"
              :disabled="false"
              @click="doViewAllProspect"
            />
          </span>
        </b-col> -->
      </b-row>
    </div>
    <div class="card__body">
      <b-row>
        <b-col style="text-align: center;">
            <span style="font-size: 15px; color: rgb(102, 102, 102); font-weight: bold;">
                {{momentDateFormatting(new Date(), 'dddd, DD MMM YYYY')}}
            </span>
        </b-col>
      </b-row>
      <b-row style="margin-top: 20px;">
        <b-col md="6" lg="6" xl="6">
          <div class="buleth__blue">
            <span>{{ M_MaintStatus.schedule}}</span>
          </div>
          <div class="buleth-text" style="margin-top: 1px !important;">Scheduled</div>
        </b-col>
        <b-col md="6" lg="6" xl="6">
          <div class="buleth__yellow">
            <span>{{ M_MaintStatus.queue_up}}</span>
          </div>
          <div class="buleth-text" style="margin-top: 1px !important;">Unscheduled</div>
        </b-col>
      </b-row>
      <b-row style="margin-top: 20px; margin-bottom: 10px;">
        <b-col md="6" lg="6" xl="6">
          <div class="buleth__green">
            <span>{{ M_MaintStatus.on_progress}}</span>
          </div>
          <div class="buleth-text" style="margin-top: 1px !important;">On Progress</div>
        </b-col>
        <b-col md="6" lg="6" xl="6">
          <div class="buleth__cyan">
            <span>{{ M_MaintStatus.completed}}</span>
          </div>
          <div class="buleth-text" style="margin-top: 1px !important;">Completed</div>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      M_MaintStatus: {}
    };
  },
  computed: {
    ButtonStatus() {
      return this.$store.getters.getButtonStatus;
    },
  },
  methods: {
    getData() {
      var param = {
        option_function_cd: "GetMaintStatusDash",
        module_cd: "FM",
        ss_portfolio_id: this.getDataUser().portfolio_id,
        user_id: this.getDataUser().user_id,
        date: this.getToday()
      };
      this.CallFunction(param).then(ress => {
        if (ress == null) return;

        this.M_MaintStatus = ress.Data[0];
      });
    },
  },
  mounted() {
    this.getData();
  }
};
</script>